import { Component, OnInit } from '@angular/core';
import { ToastController } from '@ionic/angular';
import { RestApiService } from 'src/app/services/rest-api.service';
import { AuthenticateService } from 'src/app/services/authentication.service';

@Component({
  selector: 'app-unverifiedplans',
  templateUrl: './unverifiedplans.page.html',
  styleUrls: ['./unverifiedplans.page.scss'],
})
export class UnverifiedplansPage implements OnInit {

  pendingPlans: Array<any> = [];

  constructor( public toastController: ToastController,public restApi: RestApiService, public authService: AuthenticateService) { }

  ngOnInit() {
    this.getPending();
  }

  getPending() {
    this.restApi.post('accessor/get-pending-plan', {}).subscribe((res: any) => {
      if (res && res.status) {
        if (res.status == 'success') {
          this.pendingPlans = res.data;
          console.log(this.pendingPlans);
        } else {
          this.toastController.create({
            message: res.message,
            duration: 2000
          }).then(toast => toast.present());
        }
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }

  unverified(x) {
    this.restApi.post('accessor/unverify-pending-plan', { plan_id: x.id }).subscribe((res: any) => {
      if (res && res.status) {
        if (res.status == 'success') {
            this.getPending();
        } else {
          this.toastController.create({
            message: res.message,
            duration: 2000
          }).then(toast => toast.present());
        }
      }
    }, error => {
      this.toastController.create({
        message: 'Something went wrong.',
        duration: 2000
      }).then(toast => toast.present());
    });
  }
}
